.cloud {
    width: 100%;
    height: 100vh;
    position: relative;
    background: rgba(0, 0, 0, .3);
}

.cloud:before {
    content: '';
    position: absolute;
    /*background: url('../../assets/cloud-bg.png') no-repeat center center/cover;*/
    background: url('https://images.unsplash.com/photo-1633098096956-afdc8bcc8552?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8MTMzfHxoYWNrZXJ8ZW58MHx8MHx8&auto=format&fit=crop&w=500&q=60') no-repeat center center/cover;
    height: 100%;
    width: 100%;
    z-index: -1;
}

.cloud .content {
    position: absolute;
    left: 0;
    top: 30%;
    width: 50%;
    padding: 1rem;
    background: rgba(255, 255, 255, .2);
}

.cloud h2 {
    color: #f8f8f8;
}

.cloud p {
    color: #f8f8f8;
    margin: 1rem 0;
}

.cloud span {
    color: #1c1b1d;
    margin: 1rem 0;
}

button {
    font-family: 'Roboto', sans-serif;
    padding: .7rem 1.5rem;
    margin: 1rem 0;
    background-color: #1c1b1d;
}

@media screen and (max-width: 940px) {
    .cloud .content {
        left: 0;
        width: 100%;
    }
}